import React from "react";
import "../card/cardStyle.css";
// import { Button } from 'reactstrap';
import Logo from "../imagens/Logo.png";
import Face from "../imagens/facebranco.png";

//-L: Caso seja necessario colocar mais informações adicione mais uma categoria
//no homePage na area dos cards e aqui tbm.
function Card() {
  return (
    <div className="card">
      <div className="position">
        <p className="img-container">
          <img src={Logo} alt="" />
        </p>
        <p className='desc-um'>Cadastre-se para ver fotos e vídeos dos seus amigos.</p>
        <button className="botao-branco"><img src={Face} className="branco-container" alt=""/>Entrar com o Facebook</button>
        <p className="card-container-separacao">
          <span>OU</span>
        </p>
        <input
          className="email"
          placeholder="Número do celular ou email"
        />
        <input className="completo" placeholder="Nome completo" />
        <input className="user" placeholder="Nome de usuário" />
        <input className="senha" placeholder="Senha" />
        <button className="botao">Cadastre-se</button>
        <p className='desc-dois'><span>Ao se cadastrar, você concorda com nossos</span> Termos, Política de Dados e Política de Cookies.</p>

      </div>
    </div>
  );
}

export default Card;
